.products__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.products__item {
  border-top: 1px solid #ebebeb;
  padding-bottom: 24px;
}

.products__link {
  text-decoration: none;
  display: grid;
  grid-template-columns: 140px 76px;
  width: 280px;
  margin: 0 auto;
  padding-top: 19px;
  padding-bottom: 13px;
}

.products__title-link {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  align-items: center;
  text-transform: uppercase;
  color: #222222;
  margin: 0;
  padding-top: 2px;
}

.products__table-wrapper {
  width: 280px;
  margin: 0 auto;
  margin-top: -70px;
}

.products__table {
  width: 142px;
  height: 44px;
  border-collapse: collapse;
  margin-left: 140px;
  margin-bottom: 22px;
}

.products__line {
  width: 141px;
  height: 12px;
}

.products__cell {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  padding: 0;
}

.products__cell--meaning {
  text-align: end;
}

.products__button {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  margin: 0 auto;
}

.products__more-title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #111111;
}

.products__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #444444;
  margin-top: -8px;
  margin-bottom: 21px;
}

.products__button-all {
  background-color: #f2f2f2;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
  text-decoration: none;
  margin: 0 auto;
}

.products__title-wrapper {
  width: 320px;
  margin: 0 auto;
}

.products__plus-mobile {
  padding-left: 145px;
  padding-top: 25px;
  padding-bottom: 3px;
}

.products__item--more {
  border-bottom: 1px solid #ebebeb;
}
